import NavBar from '../components/NavBar/NavBar';
import React from 'react';
import styled from '@emotion/styled';
import {PhotoContainer,ImageContainer} from '../components/PhotoGrid/components';

const Post = (data: any) => {
    const {postTitle,postMainImage,postCreationDate,postBody} = data
    return(
        data && 
        <div>
            <NavBar/>
            <PostHeader photo = {postMainImage.fields.file.url} >
                <PostTitleContainer>
                    {postTitle}
                </PostTitleContainer>
                    {/* <PostHeaderImage src = {postMainImage.fields.file.url}/> */}
            </PostHeader>
                
                <PostDateContainer>
                    {postCreationDate}
                </PostDateContainer>
                <PostBodyContainer>
                    {postBody}
                </PostBodyContainer>  
        </div>
    )
}

interface BackgroundImage {
    photo: URL;
}



